.content {
  flex: 1;
  width: 100%;
  box-sizing: border-box; /* Учитывает padding и border в ширине */
  overflow-x: hidden; /* Скрывает горизонтальную прокрутку */
}

@media (max-width: 768px) {
  .content {
    padding: 0.5em; /* Уменьшение отступов для мобильных устройств */
  }
}

.back{
  background: rgba(204, 204, 204, 0.151); /* Полупрозрачный белый фон */
    backdrop-filter: blur(20px); /* Размытие фона */
}