.schedule {
  width: 100%;
  max-width: 850px; /* Уменьшаем максимальную ширину */
  margin: 0 auto;
  padding: 0.8em; /* Уменьшаем отступы */
  border-radius: 12px;
  background-color: #2c2f36;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
}

.scheduleDate {
  background-color: #ff6f61;
  border-radius: 12px 12px 0 0;
  text-align: center;
}

.scheduleDateNum {
  color: white;
  font-size: 2.2em; /* Уменьшаем шрифт */
  font-weight: 600;
  padding: 1.2em;
}

.scheduleWeeks {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.3em;
  padding: 0.8em 0;
  color: #f0f0f0;
  font-weight: 500;
  text-transform: uppercase;
}

.scheduleWeek {
  text-align: center;
  padding: 0.3em;
  font-size: 0.9em; /* Уменьшаем шрифт */
}
.scheduleTitleText { 
  color: white;
  text-align: center;
}
.scheduleDays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.3em;
}

.scheduleDay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1em;
  border-radius: 10px;
  background-color: #1f2229;
  color: #ffffff;
  transition: background-color 0.2s ease, transform 0.15s ease;
  cursor: pointer;
}

.scheduleDay:hover {
  background-color: #ff6f61;
  transform: scale(1.03);
}

.scheduleDay .day {
  font-size: 1.3em;
  font-weight: 600;
}

.scheduleDay .event {
  background-color: rgba(255, 255, 255, 0.85);
  padding: 0.3em 0.5em;
  border-radius: 6px;
  color: #333;
  font-size: 0.85em;
  text-align: center;
}

.prevMonthDay {
  background-color: #3a3d46;
  color: #888;
}

@media (max-width: 768px) {
  .schedule {
    padding: 0.6em;
  }

  .scheduleWeeks, .scheduleDays {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5em;
  }

  .scheduleDay {
    padding: 0.9em;
  }

  .scheduleDay .day {
    font-size: 1.1em;
  }

  .scheduleDay .event {
    font-size: 0.75em;
  }
}

@media (max-width: 480px) {
  .scheduleWeeks, .scheduleDays {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.4em;
  }

  .scheduleDay {
    padding: 0.7em;
  }

  .scheduleDay .day {
    font-size: 1em;
  }

  .scheduleDay .event {
    font-size: 0.7em;
  }
}
