.mainContainer {
    display: flex;
    max-width: 1300px;
    margin: 0 auto;
    justify-content: space-between;
    gap: 1em;
    height: 105vh;
    margin-top: 1em;
}
.con { 
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 0.1em;
    flex: 1;
}

@media (max-width: 480px) { 
    .mainContainer {
        display: flex;
        max-width: 1300px;
        margin: 0 auto;
        justify-content: space-between;
        gap: 1em;
        height: 105vh;
        margin-top: 0.5em;
    }
}