.taskDetail {
    background-color: #1E2126;
    border: 1px solid #ffffff08;
    border-radius: 2em;
    padding: 2em;
    color: white;
  }
  .taskDetailTitle { 
    display: flex;
    align-items: center;
    gap: 1em;
    margin-bottom: 2em

  }
  .taskTitle {
    font-size: 1.4em;
    font-weight: 500;
  }
  .taskDescription {
    font-size: 0.9em;
    font-weight: 400;
    color: rgb(154, 242, 14);
  }
  .loader {
    font-size: 1.5rem;
    color: #777;
    text-align: center;
    margin-top: 50px;
  }
  
  .subtaskList {
    list-style: none;
    padding: 0;
  }
  
  .subtaskItem {
    padding: 5px 0;
  }
  
  .subtaskForm {
    margin-top: 20px;
  }
  
  .subtaskForm input {
    padding: 8px;
    width: 300px;
    margin-right: 10px;
  }
  
  .subtaskForm button {
    padding: 8px 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .subtaskForm button:hover {
    background-color: #45a049;
  }

  /* Пример стилей для модального окна */
.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 500px;
    width: 100%;
    
    background-color: #1E2126;
    border: 1px solid #ffffff08;
    border-radius: 2em;
    padding: 2em;
    color: white;
  }
  .modalUp { 
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .modalUpInfo { 
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1em;
  }
  .modalUpInfoTitle { 
    font-size: 1.1em;
    font-weight: 400;
    color: rgb(154, 242, 14);
  }
  .closeButton { 
    position: absolute;
    right: 2em;
    top: 2em;
    cursor: pointer;
    width: 1.4em;
  }
  .resultMessage { 
    text-align: center;
    font-size: 1em;
    color: rgb(154, 242, 14);
    font-weight: 600;
    margin-top: 1em;
  }
  .modalLine{ 
    height: 0.01em;
    width: 100%;
    background-color: #4c545f66;
    border-radius: 1em;
  }
  .modalUpTitle { 
    font-size: 1.1em;
    font-weight: 500;
    margin: 1.5em 0;
  }
  .optionsList { 
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    margin-top: 1em;
  }
  .optionButton { 
    text-align: left;
    border: 0;
    background-color: #262a30;
    border: 1px solid #ffffff0d;
    color: white;
    padding: 1em;
    border-radius: 1em;
    width: 100%;
    transition: background-color .1s ease-in-out;

  }
  .selectedCorrect  { 
    text-align: left;
    border: 0;
    background-color: #131518;
    border: 1px solid #ffffffde;
    color: white;
    padding: 1em;
    border-radius: 1em;
    width: 100%;
  }
  .selectedIncorrect {
    text-align: left;
    border: 0;
    background-color: #131518;
    border: 1px solid #ffffffde;
    color: white;
    padding: 1em;
    border-radius: 1em;
    width: 100%;
  }
  .submitAnswerButton { 
    margin-top: 1em;
    border: 0;
    background-color: rgb(154, 242, 14);
    color: rgb(21, 21, 21);
    padding: 0.7em 2em;
    border-radius: 1em;
    font-size: 0.9em;
    font-weight: 700;
    transition:background-color .15s ease-in-out ;
  }
  .submitAnswerButton:hover { 
    background-color: rgb(127, 198, 13);
  }
  .optionButton:hover { 
    background-color: #131518;
  }
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.706);
  }
  
  .subtaskText{
    cursor: pointer;
    font-size: 1.2em;
    font-weight: 400;
    letter-spacing: 0.08em;
    transition: color .15s ease-in-out;
  }
  .subtaskText:hover{
    color: rgb(154, 242, 14);
  }

  .deleteButton {
    background-color: #f44336; /* Красный цвет для кнопки удаления */
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    margin-left: 10px;
    border-radius: 4px;
  }
  
  .deleteButton:hover {
    background-color: #d32f2f;
  }
  


