.navbar {
  user-select: none;
  width: 230px; /* Ширина навбара */
  height: 20vh;
  border-radius: 2em;
  box-sizing: border-box; /* Учитывает padding и border в ширине */
  background-color: #1E2126;
  display: flex;
  flex-direction: column;
  gap: 1em;
  padding: 1em;
  border: 1px solid #ffffff08;
  z-index: 1;
}

.disabled {
  background-color: rgba(30, 33, 38, 0.5); /* Полупрозрачный фон */
  pointer-events: none; /* Отключаем взаимодействие */
  position: relative; /* Для позиционирования закрытого сообщения */
}

.closedMessage {
  color: white;
  font-size: 1em;
  font-weight: bold;
  text-align: center;
  pointer-events: none; /* Отключаем взаимодействие с текстом */
}

.navbarItem {
  text-decoration: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  width: 100%;
  gap: 0.5em;
  border-radius: 1em;
  padding: 0.5em 0;
}

.navbarItem:hover {
  background-color: #25272fef;
}

.navbarItemSvg {
  width: 1.5em;
  stroke-width: 2;
  margin-left: 1.5em;
}

.navbarItemText {
  color: white;
  text-decoration: none;
  font-size: 0.9em;
  font-weight: 400;
}

/* Мобильные стили */
@media (max-width: 768px) {
  .navbar {
    width: 100%;
    height: auto;
    border-radius: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0.4em 0;
    position: fixed;
    bottom: 0;
    background-color: #1E2126;
    border: none;
    box-shadow: 0 -1px 5px rgba(0,0,0,0.3);
  }
  
  .navbarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    width: 100%;
    border-radius: 1em;
    padding: 0.3em;
  }
  
  .navbarItemSvg {
    width: 1.7em;
    margin-left: 0em;
  }
  
  .navbarItemText {
    color: white;
    text-decoration: none;
    font-size: 0.7em;
    font-weight: 400;
  }
}
.closedMessage { 
  font-size: 0.9em;
  font-weight: 400;
  padding: 1em;
  position: absolute;
  left: 0;
  right: 0;
}
.disabled { 
  background-color: #1E2126;
  height: 5em;
}