.cart {
    cursor: pointer;
    position: relative;
    border-radius: 2em;
    color: #fff; /* Цвет текста */
    background-size: cover;
    transition: transform .2s ease-in-out;
}
/* carttask.module.css */
.cartImage {
    cursor: pointer;
    position: relative;
    color: #fff; /* Цвет текста */
    transition: transform .2s ease-in-out;
  }
.cartImage img {
    width: 18em;
    height: 11em;
    border-radius: 2em;
    background-size: cover;
    transition: transform .2s ease-in-out;
    background-color: #5c5c5c;
    z-index: 0;
  }
  
.cart:hover {
    transform: translateY(-0.2em);
}
.cartUp { 
    position: absolute;
    top: 1em;
    left: 1em;
    right: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.cartDecision { 
    display: inline-block;
    background-color: #efefefe2;
    border-radius: 2em;
    top: 1em;
    right: 1em;
}
.cartDecisionSvg { 
    padding: 0.4em;
    width: 1.2em;
}
.cartTitle {
    top: 1em;
    left: 1em;
    display: inline-block;
    font-size: 0.8em;
    font-weight: 600;
    padding: 0.7em 1em;
    border-radius: 1em;
    background-color: rgb(154, 242, 14); 
    color: #130500;
}

.cartBackMirror {
    position: absolute;
    bottom: 1em;
    right: 1em;
    left: 1em;
    padding: 1em;
    font-size: 0.75em;
    font-weight: 400;
    line-height: 1.2em;
    border-radius: 1.5em;
    background: rgba(204, 204, 204, 0.151); /* Полупрозрачный белый фон */
    backdrop-filter: blur(20px); /* Размытие фона */
    color: rgb(251, 239, 239);
}


@media (max-width: 480px) {
    .cart {
        cursor: pointer;
        position: relative;
        border-radius: 2em;
        color: #fff; /* Цвет текста */
        background-size: cover;
        transition: transform .2s ease-in-out;
    }
    /* carttask.module.css */
    .cartImage {
        cursor: pointer;
        position: relative;
        color: #fff; /* Цвет текста */
        transition: transform .2s ease-in-out;
      }
    .cartImage img {
        width: 14em;
        height: 9em;
        border-radius: 1.3em;
        background-size: cover;
        transition: transform .2s ease-in-out;
        background-color: #5c5c5c;
      }
      
    .cart:hover {
        transform: translateY(-0.2em);
    }
    .cartUp { 
        position: absolute;
        top: 1em;
        left: 1em;
        right: 1em;
        display: flex;
        align-items: center;
        justify-content: space-between;
    
    }
    .cartDecision { 
        display: inline-block;
        background-color: #efefefe2;
        border-radius: 2em;
        top: 1em;
        right: 1em;
    }
    .cartDecisionSvg { 
        padding: 0.2em;
        width: 0.8em;
    }
    .cartTitle {
        top: 0;
        left: 1em;
        display: inline-block;
        font-size: 0.6em;
        font-weight: 600;
        padding: 0.3em 1em;
        border-radius: 1em;
        background-color: rgb(154, 242, 14); 
        color: #130500;
    }
    
    .cartBackMirror {
        position: absolute;
        bottom: 1em;
        right: 1em;
        left: 1em;
        padding: 1em;
        font-size: 0.55em;
        font-weight: 400;
        line-height: 1.2em;
        border-radius: 1.6em;
        background: rgba(204, 204, 204, 0.151); /* Полупрозрачный белый фон */
        backdrop-filter: blur(20px); /* Размытие фона */
        color: rgb(251, 239, 239);
    }
    
}