.task {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  background-color: #1E2126;
  border: 1px solid #ffffff08;
  border-radius: 2em;
  padding: 1em; /* Уменьшен padding для лучшего отображения на маленьких экранах */
  gap: 1em; /* Уменьшен gap для лучшего отображения на маленьких экранах */
  box-sizing: border-box; /* Учитывает padding и border в ширине */
}
.taskText { 
  color: white;
}

/* Адаптивные стили для телефонов */
@media (max-width: 768px) {
  .task {
    flex-direction: column;
    padding: 0.5em;
    gap: 1em; /* Увеличен gap для лучшего отображения на маленьких экранах */
  }
}
