.header {
    margin-top: 0.5em;
}
.headerTitle { 
    display: flex;
}
.headerSvg { 
    width: 3em;
    stroke: white;
    margin-right: 0.5em;
}
.headerTitleText {
    padding: 0 1em;
    cursor: pointer;
    color: white;
    font-size: 1.8em;
    font-weight: 700;
}

.headerTitleText b{
    color: rgb(154, 242, 14);
}
.con {
    position: relative;
    max-width: 1300px;
    margin: 0 auto;
    background-color: #1E2126;
    border: 1px solid #ffffff08;
    border-radius: 2em;
    padding: 1em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .userInfoBuy { 
    text-decoration: none;
    color: rgb(0, 0, 0);
    background-color:rgb(154, 242, 14);  
    padding: 0.7em 1em;
    border-radius: 0.9em;
    font-size: 0.9em;
    font-weight: 500;
  }
  .userInfoBuy:hover { 
    background-color:rgb(133, 209, 12);  
  }
  .userInfo{ 
    display: flex;
    align-items: center;
    gap: 2em;
    margin-right: 2em;
  }
  
.btn { 
    font-size: 0.94em;
    font-weight: 600;
    border: 1px solid white;
    padding: 0.5em 2em;
    border-radius: 0.9em;
    color: white;
    margin-right: 2em;
    background:none;
    transition: background-color .3s ease-in-out, color .3s ease-in-out;
}
.btn:hover { 
    color: rgb(0, 0, 0);
    background-color: white;
}
.formOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.formContainer {
    background: white;
    padding: 2em;
    border-radius: 0.6em;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    width: 300px;
    position: relative;
}
.closeButton {
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    background: none;
    border: none;
    font-size: 1.5em;
    color: #aaa;
    cursor: pointer;
}
.closeButton:hover {
    color: black;
}
.form {
    display: flex;
    flex-direction: column;
}
.form label {
    margin-bottom: 1em;
}
.form input {
    margin-top: 0.5em;
    padding: 0.5em;
    border: 1px solid #ccc;
    border-radius: 0.3em;
    width: 100%;
}
.form button {
    margin-top: 1em;
    padding: 0.5em;
    border: none;
    border-radius: 0.3em;
    background-color: #1E2126;
    color: white;
    cursor: pointer;
    font-size: 1em;
}
.form button:hover {
    background-color: #14161a;
}
.toggleForm {
    color: rgb(0, 0, 0);
    cursor: pointer;
    margin-top: 1em;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .toggleFormA { 
    font-size: 1em;
    color: #ffffff;
    font-weight: 400;
    padding: 0.4em 1em;
    border-radius: 0.5em;
    background-color: #1E2126;
}
  .errorMessage {
    color: red;
    font-size: 0.9em;
    margin-top: 0.5em;
  }
  
  .userInfoBalance { 
    font-size: 0.94em;
    font-weight: 400;
    border: 1px solid white;
    padding: 0.6em 1em;
    border-radius: 0.9em;
    color: white;
    background:none;
    cursor: pointer;
  }

