.home {
    gap: 8em;
    background-color: #131212;
    color: white;
  }
  .con { 
    max-width: 1235px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #1E2126;
    border: 1px solid #ffffff08;
    border-radius: 1em;
    padding: 2em;
}
  .homeTitle {
    color: white;
    font-size: 2em;
    font-weight: 700;
    margin-bottom: 0.8em;
  }
  .leftSide {
    max-width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  
  .labels {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .label {
    background-color: #ffffff;
    padding: 0.8em 1.5em;
    border-radius: 2em;
    color: black;
    font-weight: 600;
  }
  
  .leftSideTitle {
    font-size: 3em;
    margin-bottom: 20px;
    font-weight: 800;
    margin-top: 1em;
    
  }
  
  .desc {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 20px;
    margin-top: 2em;
  }
  .flex { 
    display: flex;
    align-items: center;
    gap: 0.5em;
  }
  .benefitsTitel {
    font-weight: 700;
    margin-bottom: 0.5em;
  }
  .benefits {
    margin-top: 3em;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
  }
  
  .benefit {
    display: flex;
    align-items: center;
    gap: 1em;
    background-color: #414141;
    padding: 1.9em 1.5em;
    border-radius: 1.5em;
    font-size: 0.8em;
    font-weight: 500;
    line-height: 1.3em;
    width: 23em;
  }
  
  .price {
    text-align: center;
    padding: 0.6em 3em;
    font-size: 1.3em;
    color: rgb(0, 0, 0);
    background-color: rgb(154, 242, 14);
    border-radius: 1em;
    font-weight: 600;
  }
  .priceInfo { 
    margin-top: 2em;
    display: flex;
    align-items: center;
    gap: 2em;
  }
  .priceBtn { 
    text-align: center;
    padding: 0.6em 5em;
    font-size: 1.3em;
    color: rgb(0, 0, 0);
    background-color: rgb(154, 242, 14);
    border-radius: 1em;
    font-weight: 600;
    text-decoration: none;
    transition:background-color .2s ease-in-out, transform .2s ease-in-out ;
  }
  .priceBtn:hover { 
    background-color: rgb(97, 152, 8);
    transform: translateY(-0.1em);
  }
  .price b {
    font-size: 0.9em;
    color: rgb(0, 0, 0);
    border-radius: 1em;
    font-weight: 300;
    margin-right: 0.5em;
  }
  
  .oldPrice {
    text-decoration: line-through;
    text-decoration-thickness: 1.1px; /* Толщина линии */
    text-decoration-color: rgb(0, 0, 0); /* Цвет линии */
  }
  
  
  .rightSide {
    
  }
  
  .form { 
    position: relative;
    background-color: #2b2b2b;
    padding: 4em 1.5em;
    border-radius: 1.6em;
    color: black;
    max-width: 350px;
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.formTitle { 
    font-size: 1.5em;
    font-weight: 700;
    margin-bottom: 0.3em;
    color: white;
}

.rightSideDesc { 
    font-size: 1em;
    font-weight: 500;
    margin-bottom: 1em;
    color: white;
}

.input { 
    border: 1px solid rgb(247, 247, 247);
    padding: 1em;
    border-radius: 0.8em;
    background-color: #00000094;
    width: 100%;
    margin-bottom: 1em;
    box-sizing: border-box; /* Важно для предотвращения выхода за блок */
}

.btn { 
    border: 0;
    background-color: rgb(0, 132, 255);
    padding: 1em;
    width: 100%;
    border-radius: 0.8em;
    color: white;
    font-weight: 1em;
    font-weight: 600;
    transition:  background-color .4s ease, color .4s ease;
}
.btn:hover { 
    background-color: rgb(255, 255, 255);
    color: black;
}

/* Стили для больших экранов остаются */
.home {
  gap: 8em;
  padding: 1em 10em;
  background-color: #131212;
  color: white;
}

.con {
  max-width: 1235px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1E2126;
  border: 1px solid #ffffff08;
  border-radius: 1em;
  padding: 2em;
}

/* Медиазапросы для планшетов */
@media (max-width: 1024px) {
  .con {
      flex-direction: column; /* Меняем на вертикальный флекс */
      align-items: flex-start;
      padding: 2em 1.5em;
  }

  .leftSide {
      max-width: 100%;
  }

  .benefit {
      width: 100%; /* Ширина на всю ширину контейнера */
  }

  .price {
      margin-top: 1.5em;
  }
}

/* Медиазапросы для мобильных устройств */
@media (max-width: 480px) {
  .home {
      padding: 1em 0.5em; /* Уменьшаем боковые отступы */
  }

  .con {
    max-width: 1035px;
    background-color: #1E2126;
    border: 1px solid #ffffff08;
    border-radius: 1em;
    padding: 1em;
  }
  .labels {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  
  .label {
    background-color: #ffffff;
    padding: 0.6em 1.2em;
    border-radius: 0.9em;
    color: black;
    font-weight: 500;
    font-size: 0.7em;
  }

  .leftSideTitle {
    font-size: 1.3em;
    font-weight: 800;
    margin-top: 1em;
    text-align: center;
    
  }

  .desc {
    font-size: 0.9em;
    line-height: 1.6;
    text-align: center;
    font-weight: 400;
  }

  .flex { 
    display: flex;
    align-items: center;
    gap: 0.2em;
  }
  .benefitsTitel {
    font-weight: 700;
    margin-bottom: 0.5em;
  }
  .benefits {
    margin-top: 3em;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 0.2em;
  }
  .benefitImg { 
    width: 3.5em;
  }
  .benefit {
    display: flex;
    align-items: center;
    gap: 0.8em;
    background-color: #414141;
    padding: 1.9em 1.5em;
    border-radius: 1.5em;
    font-size: 0.6em;
    font-weight: 400;
    line-height: 1.2em;
    height: 4em;
  }
  .price {
    text-align: center;
    padding: 0.6em;
    font-size: 1em;
    color: rgb(0, 0, 0);
    background-color: rgb(154, 242, 14);
    border-radius: 1em;
    font-weight: 600;
    width: 100%;
    box-sizing: border-box;
  }
  .priceInfo { 
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1em;
  }
  .priceBtn { 
    text-align: center;
    width: 100%;
    box-sizing: border-box;
    padding: 0.6em 2em;
    font-size: 1em;
    color: rgb(0, 0, 0);
    background-color: rgb(154, 242, 14);
    border-radius: 1em;
    font-weight: 600;
    text-decoration: none;
    transition:background-color .2s ease-in-out, transform .2s ease-in-out ;
  }
  .priceBtn:hover { 
    background-color: rgb(97, 152, 8);
    transform: translateY(-0.1em);
  }
  .price b {
    font-size: 0.9em;
    color: rgb(0, 0, 0);
    border-radius: 1em;
    font-weight: 300;
    margin-right: 0.5em;
  }

  .form {
      padding: 2em 1em; /* Уменьшаем отступы формы */
      max-width: 100%;
  }

  .btn {
      padding: 0.8em; /* Уменьшаем кнопку */
  }
  .img {
    width: 17em;
    display: none;
  }
}
